const Section = ({ children, name, subtitle, title }) => {
  return (
    <InView triggerOnce={true}>
      {({ inView, ref }) => {
        return (
          <div
            ref={ref}
            name={name}
            style={{
              transition: 'opacity 2000ms ease-out 100ms',
              opacity: inView ? 1 : 0
            }}
          >
            <Grid container my='25vh' justifyContent='center' spacing={8}>
              <Grid item md={4} xs={12}>
                <Box
                  display='flex'
                  justifyContent={{
                    md: 'flex-start'
                    // xs: 'center'
                  }}
                >
                  <Box>
                    <Box display='flex' alignItems='center'>
                      <Box
                        width={40}
                        height={1}
                        mr={1}
                        sx={{
                          border: '1px solid #ef8468'
                        }}
                      />
                      <Typography
                        variant='subtitle1'
                        color='primary'
                        fontWeight={300}
                      >
                        {subtitle}
                      </Typography>
                    </Box>
                    <Typography variant='h2' color='text.primary'>
                      {title}
                    </Typography>
                  </Box>
                </Box>
              </Grid>
              <Grid item md={6} xs={12}>
                <Box
                  sx={{
                    transition: 'transform 2000ms ease-out 100ms',
                    transform: `translateX(${inView ? '0' : '100'}px)`
                  }}
                >
                  {children}
                </Box>
              </Grid>
            </Grid>
          </div>
        );
      }}
    </InView>
  );
};

